@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900";
#bookmarks {
  background-color: #323232;
  width: 20rem;
  height: 95vh;
  padding: 10px;
  display: none;
}

.bookmark-button {
  cursor: pointer;
  background-color: #fc0;
  border: none;
  width: 10rem;
  padding: 10px;
  font-size: 16px;
  display: none;
}

.bookmark-button:hover {
  background-color: #e6b800;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: roboto;
}

body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

main {
  justify-content: center;
  display: flex;
}

a {
  color: #40779a;
  text-decoration: none;
}

#menu {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

li {
  list-style: none;
}

h1, h3, p {
  color: #f5f5f5;
}

#title-red {
  color: #de0000;
}

#wrapper {
  width: 100vw;
  height: 100vh;
  position: relative;
}

#wrapper:before {
  content: "";
  background-image: var(--bg-url);
  filter: blur(2px);
  z-index: -1;
  background-position: center;
  background-size: cover;
  width: calc(100% + 40px);
  height: calc(100% + 40px);
  position: absolute;
  top: -20px;
  left: -20px;
}

#wrapper:after {
  content: "";
  z-index: -1;
  background: #0009;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

header {
  justify-content: center;
  align-items: center;
  height: 75px;
  display: flex;
}

.header-content {
  justify-content: space-between;
  align-items: center;
  width: 80%;
  display: flex;
}

#header-bg {
  opacity: .7;
  z-index: -1;
  background-color: #0000;
  background-image: linear-gradient(#14181c, #14181cf1 16.56%, #14181ccc 30.85%, #14181c9b 43.77%, #14181c64 56.23%, #14181c33 69.15%, #14181c0e 83.44%, #14181c00);
  background-repeat: repeat-x;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

#main-nav {
  gap: 1em;
  display: flex;
}

.label {
  color: #dae0e7;
}

#home-name {
  font-size: 1.25rem;
}

#search-wrapper {
  position: relative;
}

#search-bar {
  letter-spacing: .2px;
  color: #000000de;
  caret-color: #0000008a;
  background-color: #ffffff40;
  border: none;
  border-radius: 4px;
  width: 36rem;
  height: 2rem;
  padding-left: 5px;
  font-size: 14px;
  line-height: 25px;
  position: relative;
}

#search-bar:focus {
  background-color: #f8f8f8;
  outline: none;
}

#submit {
  cursor: pointer;
  background-color: inherit;
  border: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  right: .5rem;
  transform: translateY(-50%);
}

.submit-icon {
  color: #cdcdcd;
}

#nav-search-form {
  position: relative;
}

#live-search-dropdown {
  z-index: 999;
  background-color: #1f1f1f;
  border-radius: 6px;
  width: 36rem;
  position: absolute;
  top: 35px;
}

.suggested-container {
  border-bottom: 1px solid #656565;
  align-items: center;
  height: 100px;
  display: flex;
}

.suggested-container:last-child {
  border-bottom: none;
}

.suggested-container:hover {
  cursor: pointer;
  background-color: #313131;
}

.suggested-poster-container {
  border-radius: 12px;
  width: 55px;
  height: 80%;
  margin: 0 15px 0 7px;
  overflow: hidden;
}

.suggested-poster {
  height: 100%;
}

.suggested-poster-fallback {
  width: 100%;
  height: 100%;
}

.suggested-info {
  color: #b5b5b5;
  font-size: 14px;
}

.suggested-year {
  margin-top: .3em;
  margin-bottom: .3em;
}

.movie-container {
  gap: 70px;
  width: 80%;
  margin: 5em 0;
  display: flex;
  position: relative;
}

.poster {
  outline-offset: -2px;
  border-radius: 15px;
  outline: 2px solid #ffffff3f;
  height: 550px;
  box-shadow: 0 0 25px #000c;
}

.movie-details {
  flex-direction: column;
  height: 100%;
  padding-top: .4rem;
  display: flex;
}

.movie-title {
  text-shadow: 2px 2px 2px #0003, -1px -1px 2px #0003;
  text-wrap: balance;
  padding-top: 3rem;
  font-size: 4rem;
  font-weight: 450;
}

.genre-year-age-time {
  align-items: center;
  padding: 2rem 0 3rem;
  display: flex;
}

.genres-container {
  flex-direction: row;
  gap: 10px;
  padding-right: 2rem;
  font-size: .875rem;
  display: flex;
}

.genres {
  background-color: #212020;
  border-radius: 1rem;
  padding: .5rem 1rem;
}

.description {
  color: #dbdbdb;
  width: 75%;
  line-height: 1.7rem;
}

.release-date, .age-rating {
  color: #dbdbdb;
}

.age-rating:before, .age-rating:after {
  content: "•";
  padding: 0 .6rem;
  font-size: .875rem;
}

.run-time {
  color: #dbdbdb;
}

.homepage {
  display: none;
}
/*# sourceMappingURL=Movie-Database.d077753f.css.map */
